import React from 'react';
import Link from 'next/link';

import Icon from '../Icon';

import styles from './style.scss';

function getSVGFill(color) {
  const path = {
    fill: color,
  };
  return path;
}

const MenuItem = props => {
  const { title, url, icon, active, handleClick } = props;
  const iconSize = 60;

  const renderIcon = () => {
    if (active) {
      return (
        <Icon size={iconSize} color="#EE86B7">
          {icon.map(i => (
            <path d={i} key={i} style={getSVGFill('#EE86B7')} />
          ))}
        </Icon>
      );
    }

    return (
      <Icon size={iconSize} color="#6E7376">
        {icon.map(i => (
          <path d={i} key={i} style={getSVGFill('#6E7376')} />
        ))}
      </Icon>
    );
  };

  return (
    <Link href={url}>
      <div className={styles.menu_item} onClick={handleClick}>
        <span className={styles.icon}>{renderIcon()}</span>
        <span className={styles.title}>{title}</span>
      </div>
    </Link>
  );
};

export default MenuItem;
